document.addEventListener('DOMContentLoaded', () => {
  const Numbers = document.querySelector(".pagination__list");
  const paginatedList = document.querySelector(".store__list");
  const listItems = paginatedList?.querySelectorAll("li");
  const nextButton = document.querySelector(".pagination__button_next");
  const prevButton = document.querySelector(".pagination__button_prev");
  const paginationLimit = 12;
  const pageCount = Math.ceil(listItems?.length / paginationLimit);
  let currentPage = 1;

  const disableButton = (button) => {
    button.classList.add("pagination__button_disabled");
    button.setAttribute("disabled", true);
  };

  const enableButton = (button) => {
    button.classList.remove("pagination__button_disabled");
    button.removeAttribute("disabled");
  };

  const handlePageButtonsStatus = () => {
    if (currentPage === 1) {
      disableButton(prevButton);
    } else {
      enableButton(prevButton);
    }

    if (pageCount === currentPage) {
      disableButton(nextButton);
    } else {
      enableButton(nextButton);
    }
  };

  const handleActivePageNumber = () => {
    document.querySelectorAll(".number").forEach((button) => {
      button.classList.remove("pagination__button_active");
      const pageIndex = Number(button.getAttribute("page-index"));
      if (pageIndex == currentPage) {
        button.classList.add("pagination__button_active");
      }
    });
  };

  const appendPageNumber = (index) => {
    const pageNumber = document.createElement("button");
    pageNumber.className = "number pagination__button";
    pageNumber.innerHTML = index;
    pageNumber.setAttribute("page-index", index);
    pageNumber.setAttribute("aria-label", "Page " + index);

    Numbers.appendChild(pageNumber);
  };

  const getPaginationNumbers = () => {
    for (let i = 1; i <= pageCount; i++) {
      appendPageNumber(i);
    }
  };

  const setCurrentPage = (pageNum) => {
    currentPage = pageNum;
    if (paginatedList) {
      handleActivePageNumber();
      handlePageButtonsStatus();

      const prevRange = (pageNum - 1) * paginationLimit;
      const currRange = pageNum * paginationLimit;

      listItems.forEach((item, index) => {
        item.classList.add("is-hidden");
        if (index >= prevRange && index < currRange) {
          item.classList.remove("is-hidden");
        }
      });
    }
  };


  window.addEventListener("load", () => {
    getPaginationNumbers();
    setCurrentPage(1);
    if (paginatedList) {
      prevButton.addEventListener("click", () => {
        setCurrentPage(currentPage - 1);
      });

      nextButton.addEventListener("click", () => {
        setCurrentPage(currentPage + 1);
      });

      document.querySelectorAll(".number").forEach((button) => {
        const pageIndex = Number(button.getAttribute("page-index"));

        if (pageIndex) {
          button.addEventListener("click", () => {
            setCurrentPage(pageIndex);
          });
        }
      });
    }
  });
});

