document.addEventListener('DOMContentLoaded', () => {
  /*
    1.        
    2.   ()    -  
    3.      ,   -  
  */

  const dropdownBtns = document.querySelectorAll('.nav__btn');
  const drops = document.querySelectorAll('.dropdown');

  dropdownBtns.forEach(el => {
    el.addEventListener('click', (e) => {
      let currentBtn = e.currentTarget;
      let drop = currentBtn.closest('.nav__item').querySelector('.dropdown');
      // let dropMenuHeight = drop.clientHeight;
      // console.log('dropMenuHeight', dropMenuHeight);


      dropdownBtns.forEach(el => {
        if (el !== currentBtn) {
          el.classList.remove('nav__btn--active');
        }
      });

      drops.forEach(el => {
        if (el !== drop) {
          el.classList.remove('dropdown--active');
        }
      });

      if (drop.classList.contains('dropdown--active')) {
        // drop.style.position = 'absolute';
        drop.classList.remove('dropdown--active');
      } else {
        // drop.style.position = 'relative';
        drop.classList.add('dropdown--active');
      }
      currentBtn.classList.toggle('nav__btn--active');
    });
  });

  document.addEventListener('click', (e) => {
    if (!e.target.closest('.nav-dropdown')) {
      dropdownBtns.forEach(el => {
        el.classList.remove('nav__btn--active');
      });

      drops.forEach(el => {
        el.classList.remove('dropdown--active');
      });
    }
  });
});
