import { disableScroll } from '../functions/disable-scroll';
import { enableScroll } from '../functions/enable-scroll';

document.addEventListener('DOMContentLoaded', () => {
  (function () {
    const burger = document?.querySelector('[data-burger]');
    const menu = document?.querySelector('[data-menu]');
    const menuItems = document?.querySelectorAll('[data-menu-item]');
    const overlay = document?.querySelector('[data-menu-overlay]');
    const mobileFooter = document?.querySelector('[data-menu-footer]');
    const drops = document.querySelectorAll('.dropdown');
    console.log(burger)
    burger?.addEventListener('click', (e) => {
      console.log(burger)
      burger?.classList.toggle('burger--active');
      menu?.classList.toggle('nav--active');
      overlay?.classList.toggle('overlay--active');
      mobileFooter.classList.toggle('mobile-footer--active');

      if (menu?.classList.contains('nav--active')) {
        burger?.setAttribute('aria-expanded', 'true');
        burger?.setAttribute('aria-label', ' ');
        disableScroll();
      } else {
        burger?.setAttribute('aria-expanded', 'false');
        burger?.setAttribute('aria-label', ' ');
        enableScroll();
      }
    });

    overlay?.addEventListener('click', () => {
      console.log('1');

      burger?.setAttribute('aria-expanded', 'false');
      burger?.setAttribute('aria-label', ' ');
      burger.classList.remove('burger--active');
      menu.classList.remove('nav--active');
      overlay.classList.remove('overlay--active');
      mobileFooter.classList.remove('mobile-footer--active');

      enableScroll();
    });

    menuItems?.forEach(el => {
      el.addEventListener('click', () => {
        burger?.setAttribute('aria-expanded', 'false');
        burger?.setAttribute('aria-label', ' ');
        burger.classList.remove('burger--active');
        menu.classList.remove('nav--active');
        overlay.classList.remove('overlay--active');
        mobileFooter.classList.remove('mobile-footer--active');

        enableScroll();
      });
    });
  })();
});
